var minSlider;
var maxSlider;

var frDiv;

function setup() {
  createCanvas(500, 500);
  colorMode(HSB, 255);
  pixelDensity(1);

  minSlider = createSlider(-5, 0, -2.5, 0.0005);
  maxSlider = createSlider(0, 2.5, 5, 0.0005);

  frDiv = createDiv("");
}

function draw() {
  var maxiterations = 100;

  loadPixels();
  for (var x = 0; x < width; x++) {
    for (var y = 0; y < height; y++) {
      var a = map(x, 0, width, minSlider.value(), maxSlider.value());
      var b = map(y, 0, height, minSlider.value(), maxSlider.value());

      var ca = a;
      var cb = b;

      var n = 0;

      while (n < maxiterations) {
        var aa = a * a - b * b;
        var bb = 2 * a * b;
        a = aa + ca;
        b = bb + cb;
        if (a * a + b * b > 16) {
          break;
        }
        n++;
      }

      var bright = map(n, 0, maxiterations, 0, 1);
      bright = map(sqrt(bright), 0, 1, 0, 255);

      if (n == maxiterations) {
        bright = 0;
      }

      var pix = (x + y * width) * 4;
      pixels[pix + 0] = 255 - bright;
      pixels[pix + 1] = 255 - bright;
      pixels[pix + 2] = 255 - bright;
      pixels[pix + 3] = 255;
    }
  }
  updatePixels();

  frDiv.html(floor(frameRate()));
}
